import React, {Component} from "react";
import axios from 'axios';
import Link from 'next/link';

class PostList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      posts: []
    };
    this.createMarkup = this.createMarkup.bind();
  }

  componentDidMount() {
    axios.get("https://freckbeauty.com/wp-json/wp/v2/posts?_embed")
      .then(posts => {
        this.setState({
          posts: posts.data
        });
      });
  }

  createMarkup(html) {
    return { __html: html };
  }

  render() {
    return (
      <div id="container">
        <div id="post-container">
          {this.state.posts.map(post => (
            <div className="card" key={post.id}>
              <Link href={post.slug} key={post.id}>
                <a>
                  <div id="ooze-news-container">
                    <div id="ooze-img-container" role="img">
                      <img src={post._embedded['wp:featuredmedia']['0'].source_url} alt="featured post" />
                    </div>
                    <div id="ooze-title-container" role="link">
                      <p>{post.title.rendered.replace(`&#8217;`, `'`).replace(`&#038;`, '&')}</p>
                      <p className="category"><i>{post._embedded['wp:term']['0']['0'].name}</i></p>
                    </div>
                  </div>
                </a>
              </Link>
            </div>
          ))}
        </div>
        <style jsx>{`
          #container {
            display: flex;
            justify-content: space-between;
            color: #e4d8c7;
            background-color: #af492f;
            height: 425px;
          }
          #post-container {
            display: flex;
            align-items: center;
          }
          #ooze-news-container {
            min-height: 370px;
          }
          #ooze-img-container {
            max-width: 300px;
            max-height: 300px;
            margin: 10px;
            overflow: hidden;
            object-fit: scale-down;
          }
          img {
            width: 150%;
          }
          #ooze-title-container {
            max-width: 300px;
            text-align: center;
          }
          a {
            color: #fff;
            text-decoration: none;
          }
          i {
            text-transform: uppercase;
          }
          `}</style>
      </div>
    );
  }
};

export default PostList;